/**
 * Mobile Menu Component
 * Handles the mobile menu toggle functionality for the UENF theme
 */

export class MobileMenu {
  constructor(options = {}) {
    // Default options
    this.options = {
      menuToggle: '.menu-toggle',
      mainNav: '.main-navigation',
      menu: '.menu',
      activeClass: 'toggled',
      menuActiveClass: 'menu-open',
      submenuToggleClass: 'menu-item-has-children',
      submenuActiveClass: 'menu-item-active',
      submenuVisibleClass: 'toggled',
      breakpoint: 992, // px
      ...options
    };
    
    // Elements
    this.menuToggle = document.querySelector(this.options.menuToggle);
    this.mainNav = document.querySelector(this.options.mainNav);
    this.menu = this.mainNav ? this.mainNav.querySelector(this.options.menu) : null;
    
    // State
    this.isOpen = false;
    
    // Bind methods
    this.init = this.init.bind(this);
    this.toggleMenu = this.toggleMenu.bind(this);
    this.closeMenu = this.closeMenu.bind(this);
    this.handleResize = this.handleResize.bind(this);
    this.handleClickOutside = this.handleClickOutside.bind(this);
    this.toggleSubmenu = this.toggleSubmenu.bind(this);
    this.closeAllSubmenus = this.closeAllSubmenus.bind(this);
    
    // Initialize
    if (this.menuToggle && this.mainNav && this.menu) {
      this.init();
    } else {
      console.warn('MobileMenu: Elementos necessários não encontrados', {
        menuToggle: this.menuToggle,
        mainNav: this.mainNav,
        menu: this.menu
      });
    }
  }
  
  /**
   * Initialize the mobile menu
   */
  init() {
    // Add event listeners
    this.menuToggle.addEventListener('click', this.toggleMenu);
    
    // Close menu when clicking outside
    document.addEventListener('click', this.handleClickOutside);
    
    // Handle window resize
    window.addEventListener('resize', this.handleResize);
    
    // Initialize submenu toggles
    this.initSubmenus();
    
    // Set initial ARIA attributes
    this.updateAriaAttributes();
    
    // Log initialization
    console.log('MobileMenu: Inicializado com sucesso');
  }
  
  /**
   * Initialize submenu toggle functionality
   */
  initSubmenus() {
    const submenuToggles = this.mainNav.querySelectorAll(
      `.${this.options.submenuToggleClass} > a, .page_item_has_children > a`
    );
    
    submenuToggles.forEach(toggle => {
      // Add click handler
      toggle.addEventListener('click', (e) => {
        const isMobile = window.innerWidth < this.options.breakpoint;
        const parentItem = toggle.parentElement;
        const isActive = parentItem.classList.contains(this.options.submenuActiveClass);
        
        if (isMobile) {
          e.preventDefault();
          this.toggleSubmenu(parentItem);
        } else if (isActive) {
          // On desktop, prevent default only if submenu is already open
          e.preventDefault();
        }
      });
      
      // Add keyboard navigation
      toggle.addEventListener('keydown', (e) => {
        if (e.key === 'Enter' || e.key === ' ') {
          e.preventDefault();
          this.toggleSubmenu(toggle.parentElement);
        }
      });
    });
  }
  
  /**
   * Toggle the mobile menu
   * @param {Event} [event] - Optional click event
   */
  toggleMenu(event) {
    if (event) {
      event.preventDefault();
      event.stopPropagation();
    }
    
    this.isOpen = !this.isOpen;
    
    // Toggle classes
    this.menuToggle.classList.toggle('active', this.isOpen);
    this.mainNav.classList.toggle(this.options.activeClass, this.isOpen);
    this.menu.classList.toggle(this.options.activeClass, this.isOpen);
    document.body.classList.toggle(this.options.menuActiveClass, this.isOpen);
    
    // Update ARIA attributes
    this.updateAriaAttributes();
    
    // Close all submenus when closing the menu
    if (!this.isOpen) {
      this.closeAllSubmenus();
    }
    
    // Toggle focus trap
    this.toggleFocusTrap();
    
    // Dispatch custom event
    const eventName = this.isOpen ? 'menuOpened' : 'menuClosed';
    document.dispatchEvent(new CustomEvent(`menu:${eventName}`, {
      detail: { menu: this.mainNav }
    }));
  }
  
  /**
   * Close the mobile menu
   */
  closeMenu() {
    if (!this.isOpen) return;
    
    this.isOpen = false;
    
    // Remove classes
    this.menuToggle.classList.remove('active');
    this.mainNav.classList.remove(this.options.activeClass);
    this.menu.classList.remove(this.options.activeClass);
    document.body.classList.remove(this.options.menuActiveClass);
    
    // Close all submenus
    this.closeAllSubmenus();
    
    // Update ARIA attributes
    this.updateAriaAttributes();
    
    // Remove focus trap
    this.toggleFocusTrap(false);
    
    // Set focus back to menu toggle
    this.menuToggle.focus();
    
    // Dispatch closed event
    document.dispatchEvent(new CustomEvent('menu:menuClosed', {
      detail: { menu: this.mainNav }
    }));
  }
  
  /**
   * Toggle submenu for mobile
   * @param {HTMLElement} menuItem - The parent menu item
   * @param {boolean} [forceState] - Force the submenu to be open (true) or closed (false)
   */
  toggleSubmenu(menuItem, forceState) {
    if (!menuItem) return;
    
    const submenu = menuItem.querySelector('.sub-menu, .children');
    const toggle = menuItem.querySelector('a');
    
    if (!submenu || !toggle) return;
    
    const isActive = menuItem.classList.contains(this.options.submenuActiveClass);
    const shouldOpen = typeof forceState === 'boolean' ? forceState : !isActive;
    
    // Update classes
    menuItem.classList.toggle(this.options.submenuActiveClass, shouldOpen);
    submenu.classList.toggle(this.options.submenuVisibleClass, shouldOpen);
    
    // Update ARIA attributes
    toggle.setAttribute('aria-expanded', shouldOpen.toString());
    
    // Close other submenus at the same level if opening a new one
    if (shouldOpen) {
      this.closeSiblingSubmenus(menuItem);
    }
    
    return shouldOpen;
  }
  
  /**
   * Close all submenus at the same level as the given menu item
   * @param {HTMLElement} menuItem - The menu item whose siblings should be closed
   */
  closeSiblingSubmenus(menuItem) {
    if (!menuItem || !menuItem.parentNode) return;
    
    const siblings = Array.from(menuItem.parentNode.children)
      .filter(child => child !== menuItem);
    
    siblings.forEach(sibling => {
      const hasSubmenu = sibling.querySelector('.sub-menu, .children');
      if (hasSubmenu) {
        this.toggleSubmenu(sibling, false);
      }
    });
  }
  
  /**
   * Close all submenus in the navigation
   */
  closeAllSubmenus() {
    const submenuParents = this.mainNav.querySelectorAll(
      `.${this.options.submenuToggleClass}, .page_item_has_children`
    );
    
    submenuParents.forEach(parent => {
      this.toggleSubmenu(parent, false);
    });
  }
  
  /**
   * Update ARIA attributes for accessibility
   */
  updateAriaAttributes() {
    if (!this.menu || !this.menuToggle) return;
    
    // Menu toggle button
    this.menuToggle.setAttribute('aria-expanded', this.isOpen.toString());
    this.menuToggle.setAttribute('aria-controls', this.menu.id || 'main-menu');
    this.menuToggle.setAttribute('aria-haspopup', 'true');
    this.menuToggle.setAttribute('aria-label', this.isOpen ? 'Fechar menu' : 'Abrir menu');
    
    // Menu container
    this.menu.setAttribute('role', 'menu');
    this.menu.setAttribute('aria-hidden', (!this.isOpen).toString());
    
    // Menu items and submenu toggles
    const menuItems = this.menu.querySelectorAll('li');
    const submenuToggles = this.menu.querySelectorAll(
      `.${this.options.submenuToggleClass} > a, .page_item_has_children > a`
    );
    
    menuItems.forEach(item => {
      item.setAttribute('role', 'none');
      const link = item.querySelector('a');
      
      if (link) {
        link.setAttribute('role', 'menuitem');
        
        // Handle submenu toggles
        if (item.classList.contains(this.options.submenuToggleClass) || 
            item.classList.contains('page_item_has_children')) {
          const submenu = item.querySelector('.sub-menu, .children');
          const isExpanded = item.classList.contains(this.options.submenuActiveClass);
          
          link.setAttribute('aria-haspopup', 'true');
          link.setAttribute('aria-expanded', isExpanded.toString());
          
          if (submenu) {
            submenu.setAttribute('role', 'menu');
            submenu.setAttribute('aria-hidden', (!isExpanded).toString());
            
            // Ensure unique IDs for ARIA relationships
            if (!submenu.id) {
              submenu.id = `submenu-${Math.random().toString(36).substr(2, 9)}`;
            }
            if (!link.id) {
              link.id = `toggle-${Math.random().toString(36).substr(2, 9)}`;
            }
            
            link.setAttribute('aria-controls', submenu.id);
            submenu.setAttribute('aria-labelledby', link.id);
          }
        }
      }
    });
  }
  
  /**
   * Toggle focus trap for better keyboard navigation
   * @param {boolean} [enable] - Enable or disable the focus trap
   */
  toggleFocusTrap(enable = this.isOpen) {
    // Remove any existing focus trap
    const existingTraps = document.querySelectorAll('.menu-focus-trap');
    existingTraps.forEach(trap => trap.remove());
    
    if (!enable) return;
    
    // Create focus trap elements
    const trapBefore = document.createElement('div');
    const trapAfter = document.createElement('div');
    
    trapBefore.className = 'menu-focus-trap';
    trapAfter.className = 'menu-focus-trap';
    
    // Insert traps around the menu
    this.mainNav.parentNode.insertBefore(trapBefore, this.mainNav);
    this.mainNav.parentNode.insertBefore(trapAfter, this.mainNav.nextSibling);
    
    // Set tabindex for focus trapping
    [trapBefore, trapAfter].forEach(trap => {
      trap.tabIndex = 0;
      trap.setAttribute('aria-hidden', 'true');
      trap.style.cssText = `
        position: absolute;
        width: 1px;
        height: 1px;
        padding: 0;
        margin: -1px;
        overflow: hidden;
        clip: rect(0, 0, 0, 0);
        border: 0;
      `;
      
      trap.addEventListener('focus', () => {
        const focusableElements = this.getFocusableElements();
        if (focusableElements.length > 0) {
          if (trap === trapBefore) {
            focusableElements[focusableElements.length - 1].focus();
          } else {
            focusableElements[0].focus();
          }
        }
      });
    });
  }
  
  /**
   * Get all focusable elements within the menu
   * @returns {HTMLElement[]} Array of focusable elements
   */
  getFocusableElements() {
    if (!this.menu) return [];
    
    const selector = [
      'a[href]',
      'button:not([disabled])',
      'input:not([disabled]):not([type="hidden"])',
      'select:not([disabled])',
      'textarea:not([disabled])',
      '[tabindex]:not([tabindex^="-"]):not([disabled])',
      'details:not([disabled]) > summary',
      'details:not([disabled])',
      'audio[controls]',
      'video[controls]',
      '[contenteditable]:not([contenteditable="false"])',
      'iframe'
    ].join(',');
    
    return Array.from(this.menu.querySelectorAll(selector))
      .filter(el => {
        // Filter out hidden elements
        const style = window.getComputedStyle(el);
        return !(el.offsetWidth <= 0 && el.offsetHeight <= 0) &&
               style.visibility !== 'hidden' &&
               style.display !== 'none';
      });
  }
  
  /**
   * Handle window resize events
   */
  handleResize() {
    // Close menu when resizing to desktop
    if (window.innerWidth >= this.options.breakpoint) {
      this.closeMenu();
    } else if (this.isOpen) {
      // Recalculate focus trap when resizing with menu open
      this.toggleFocusTrap(true);
    }
  }
  
  /**
   * Handle clicks outside the menu
   * @param {Event} e - The click event
   */
  handleClickOutside(e) {
    if (!this.isOpen) return;
    
    const target = e.target;
    const isClickInside = this.mainNav.contains(target) || 
                         target === this.menuToggle || 
                         this.menuToggle.contains(target);
    
    if (!isClickInside) {
      this.closeMenu();
    }
  }
  
  /**
   * Clean up event listeners and DOM elements
   */
  destroy() {
    // Remove event listeners
    if (this.menuToggle) {
      this.menuToggle.removeEventListener('click', this.toggleMenu);
    }
    
    document.removeEventListener('click', this.handleClickOutside);
    window.removeEventListener('resize', this.handleResize);
    
    // Remove focus traps
    const focusTraps = document.querySelectorAll('.menu-focus-trap');
    focusTraps.forEach(trap => trap.remove());
    
    // Reset ARIA attributes
    if (this.menuToggle) {
      this.menuToggle.removeAttribute('aria-expanded');
      this.menuToggle.removeAttribute('aria-controls');
      this.menuToggle.removeAttribute('aria-haspopup');
      this.menuToggle.removeAttribute('aria-label');
    }
    
    if (this.menu) {
      this.menu.removeAttribute('role');
      this.menu.removeAttribute('aria-hidden');
      
      // Reset menu items ARIA
      const menuItems = this.menu.querySelectorAll('li');
      menuItems.forEach(item => {
        item.removeAttribute('role');
        const link = item.querySelector('a');
        
        if (link) {
          link.removeAttribute('role');
          link.removeAttribute('aria-haspopup');
          link.removeAttribute('aria-expanded');
          link.removeAttribute('aria-controls');
        }
        
        const submenu = item.querySelector('.sub-menu, .children');
        if (submenu) {
          submenu.removeAttribute('role');
          submenu.removeAttribute('aria-hidden');
          submenu.removeAttribute('aria-labelledby');
        }
      });
    }
    
    // Reset state
    this.isOpen = false;
    
    // Remove classes
    if (this.menuToggle) this.menuToggle.classList.remove('active');
    if (this.mainNav) this.mainNav.classList.remove(this.options.activeClass);
    if (this.menu) this.menu.classList.remove(this.options.activeClass);
    document.body.classList.remove(this.options.menuActiveClass);
    
    // Dispatch destroyed event
    document.dispatchEvent(new CustomEvent('menu:destroyed', {
      detail: { menu: this.mainNav }
    }));
  }
}

// Auto-initialize if this file is loaded directly
if (document.readyState === 'loading') {
  document.addEventListener('DOMContentLoaded', () => new MobileMenu());
} else {
  new MobileMenu();
}
