/**
 * Sticky Header Component
 * Handles the sticky header functionality that shows/hides on scroll
 * 
 * @module StickyHeader
 * @version 1.0.0
 */

export class StickyHeader {
  /**
   * Creates a new StickyHeader instance
   * @param {Object} options - Configuration options
   */
  constructor(options = {}) {
    // Default options
    this.settings = {
      // CSS selector for the header element
      headerSelector: '.site-header',
      
      // Class to add when header should be sticky
      stickyClass: 'header-sticky',
      
      // Class to add when header should be hidden (scrolling down)
      hiddenClass: 'header-hidden',
      
      // Offset from top of page where header becomes sticky (in pixels)
      stickyOffset: 100,
      
      // Scroll threshold before showing/hiding header (in pixels)
      scrollThreshold: 5,
      
      // Whether to enable the sticky header on mobile devices
      enableOnMobile: false,
      
      // Override with provided options
      ...options
    };
    
    // DOM elements
    this.header = null;
    this.headerHeight = 0;
    
    // State
    this.isSticky = false;
    this.isHidden = false;
    this.lastScroll = 0;
    
    // Bind methods
    this.init = this.init.bind(this);
    this.handleScroll = this.handleScroll.bind(this);
    this.handleResize = this.handleResize.bind(this);
    this.updateHeaderHeight = this.updateHeaderHeight.bind(this);
    this.destroy = this.destroy.bind(this);
    
    // Initialize when DOM is ready
    if (document.readyState === 'loading') {
      document.addEventListener('DOMContentLoaded', () => this.init());
    } else {
      this.init();
    }
  }
  
  /**
   * Initialize the sticky header
   */
  init() {
    this.header = document.querySelector(this.settings.headerSelector);
    
    if (!this.header) {
      console.warn(`[StickyHeader] Header element not found with selector: ${this.settings.headerSelector}`);
      return;
    }
    
    // Initial setup
    this.updateHeaderHeight();
    
    // Check if we should enable on mobile
    const isMobile = window.innerWidth < 768; // Common breakpoint for mobile
    if (isMobile && !this.settings.enableOnMobile) {
      console.log('[StickyHeader] Disabled on mobile devices');
      return;
    }
    
    // Add event listeners
    window.addEventListener('scroll', this.handleScroll, { passive: true });
    window.addEventListener('resize', this.handleResize);
    
    // Initial check
    this.handleScroll();
    
    console.log('[StickyHeader] Initialized');
  }
  
  /**
   * Handle scroll events
   */
  handleScroll() {
    const currentScroll = window.pageYOffset || document.documentElement.scrollTop;
    const scrollDiff = Math.abs(currentScroll - this.lastScroll);
    
    // Only update if scrolled more than the threshold
    if (scrollDiff < this.settings.scrollThreshold) {
      return;
    }
    
    // Check if we've scrolled past the header
    if (currentScroll > this.settings.stickyOffset) {
      if (!this.isSticky) {
        this.header.classList.add(this.settings.stickyClass);
        this.isSticky = true;
      }
      
      // Check scroll direction
      if (currentScroll > this.lastScroll && currentScroll > this.headerHeight) {
        // Scrolling down
        if (!this.isHidden) {
          this.header.classList.add(this.settings.hiddenClass);
          this.isHidden = true;
        }
      } else {
        // Scrolling up
        if (this.isHidden) {
          this.header.classList.remove(this.settings.hiddenClass);
          this.isHidden = false;
        }
      }
    } else {
      // Not scrolled enough to be sticky
      if (this.isSticky) {
        this.header.classList.remove(this.settings.stickyClass, this.settings.hiddenClass);
        this.isSticky = false;
        this.isHidden = false;
      }
    }
    
    this.lastScroll = currentScroll;
  }
  
  /**
   * Handle window resize events
   */
  handleResize() {
    this.updateHeaderHeight();
    
    // Re-check sticky state after resize
    this.handleScroll();
  }
  
  /**
   * Update the cached header height
   */
  updateHeaderHeight() {
    if (this.header) {
      this.headerHeight = this.header.offsetHeight;
      
      // Update CSS custom property if needed
      document.documentElement.style.setProperty(
        '--header-height', 
        `${this.headerHeight}px`
      );
    }
  }
  
  /**
   * Clean up event listeners and reset styles
   */
  destroy() {
    window.removeEventListener('scroll', this.handleScroll);
    window.removeEventListener('resize', this.handleResize);
    
    if (this.header) {
      this.header.classList.remove(
        this.settings.stickyClass, 
        this.settings.hiddenClass
      );
      
      // Reset any inline styles if needed
      this.header.removeAttribute('style');
    }
    
    console.log('[StickyHeader] Destroyed');
  }
}

// Auto-initialize if this file is loaded directly
if (typeof window !== 'undefined') {
  // Create a default instance
  window.StickyHeader = StickyHeader;
  
  // Auto-initialize with default options
  document.addEventListener('DOMContentLoaded', () => {
    window.uenfStickyHeader = new StickyHeader();
  });
}

export default StickyHeader;
