/**
 * Smooth Scroll Utility
 * Handles smooth scrolling to anchor links
 */

export class SmoothScroll {
  constructor(options = {}) {
    // Default options
    this.options = {
      selector: 'a[href^="#"]',
      offset: 0,
      duration: 800,
      easing: 'easeInOutCubic',
      ...options
    };
    
    // Easing functions
    this.easings = {
      linear: t => t,
      easeInQuad: t => t * t,
      easeOutQuad: t => t * (2 - t),
      easeInOutQuad: t => t < 0.5 ? 2 * t * t : -1 + (4 - 2 * t) * t,
      easeInCubic: t => t * t * t,
      easeOutCubic: t => (--t) * t * t + 1,
      easeInOutCubic: t => t < 0.5 ? 4 * t * t * t : (t - 1) * (2 * t - 2) * (2 * t - 2) + 1,
      easeInQuart: t => t * t * t * t,
      easeOutQuart: t => 1 - (--t) * t * t * t,
      easeInOutQuart: t => t < 0.5 ? 8 * t * t * t * t : 1 - 8 * (--t) * t * t * t,
    };
    
    // Bind methods
    this.init = this.init.bind(this);
    this.handleClick = this.handleClick.bind(this);
    this.scrollTo = this.scrollTo.bind(this);
    this.getEasingFunction = this.getEasingFunction.bind(this);
    
    // Initialize
    this.init();
  }
  
  /**
   * Initialize smooth scroll
   */
  init() {
    // Add click event listeners to all matching links
    const links = document.querySelectorAll(this.options.selector);
    
    links.forEach(link => {
      // Skip if it's a link to a tab or modal
      if (link.getAttribute('data-toggle') || link.getAttribute('data-bs-toggle') || 
          link.getAttribute('aria-controls') || link.getAttribute('role') === 'tab') {
        return;
      }
      
      // Skip if the href is just '#'
      if (link.getAttribute('href') === '#') {
        return;
      }
      
      // Skip if the link has a specific class to exclude
      if (link.classList.contains('no-smooth-scroll')) {
        return;
      }
      
      // Add click event
      link.addEventListener('click', this.handleClick);
    });
  }
  
  /**
   * Handle click event on anchor links
   * @param {Event} e - The click event
   */
  handleClick(e) {
    const link = e.currentTarget;
    const targetId = link.getAttribute('href');
    
    // Only handle links that point to the same page
    if (targetId.charAt(0) === '#' && targetId.length > 1) {
      e.preventDefault();
      
      // Remove focus from the clicked link
      link.blur();
      
      // Scroll to the target
      this.scrollTo(targetId);
    }
  }
  
  /**
   * Smooth scroll to a target element
   * @param {string} target - The target selector (e.g., '#section')
   */
  scrollTo(target) {
    const targetElement = document.querySelector(target);
    
    if (!targetElement) return;
    
    const startPosition = window.pageYOffset;
    const targetPosition = targetElement.getBoundingClientRect().top + window.pageYOffset - this.options.offset;
    const distance = targetPosition - startPosition;
    const duration = this.options.duration;
    let start = null;
    
    // Get the easing function
    const easingFunction = this.getEasingFunction();
    
    // Animation function
    const step = (timestamp) => {
      if (!start) start = timestamp;
      const progress = timestamp - start;
      const progressRatio = Math.min(progress / duration, 1);
      
      // Apply easing
      const easeProgress = easingFunction(progressRatio);
      
      // Scroll to the new position
      window.scrollTo(0, startPosition + distance * easeProgress);
      
      // Continue animation if not complete
      if (progress < duration) {
        window.requestAnimationFrame(step);
      }
    };
    
    // Start the animation
    window.requestAnimationFrame(step);
  }
  
  /**
   * Get the easing function based on options
   * @returns {Function} The easing function
   */
  getEasingFunction() {
    return this.easings[this.options.easing] || this.easings.easeInOutCubic;
  }
  
  /**
   * Update options
   * @param {Object} newOptions - New options to merge with existing ones
   */
  updateOptions(newOptions) {
    this.options = { ...this.options, ...newOptions };
  }
  
  /**
   * Destroy the smooth scroll instance
   */
  destroy() {
    const links = document.querySelectorAll(this.options.selector);
    links.forEach(link => {
      link.removeEventListener('click', this.handleClick);
    });
  }
}

// Auto-initialize if this file is loaded directly
if (typeof window !== 'undefined') {
  document.addEventListener('DOMContentLoaded', () => {
    // Initialize with default options
    const smoothScroll = new SmoothScroll({
      offset: 80, // Adjust this based on your header height
      duration: 800,
      easing: 'easeInOutCubic'
    });
    
    // Make it available globally if needed
    window.uenfSmoothScroll = smoothScroll;
  });
}
