/**
 * Painel de Atalhos - Componente ES6
 * Componente de navegação rápida para o site da UENF
 * 
 * @module ShortcutPanel
 * @version 2.0.0
 */

export class ShortcutPanel {
    /**
     * Inicializa o painel de atalhos
     * @param {Object} options - Opções de configuração
     */
    constructor(options = {}) {
        // Configurações padrão
        this.settings = {
            containerSelector: '#uenf-shortcut-panel-container',
            panelSelector: '.shortcut-panel',
            iconSelector: '.shortcut-icon',
            closeButtonSelector: '.close-panel',
            activeClass: 'active',
            animationDuration: 300,
            enableSmoothScroll: true,
            ...options
        };
        
        // Elementos do DOM
        this.container = null;
        this.panel = null;
        this.icon = null;
        this.closeButton = null;
        this.isOpen = false;
        
        // Bind de métodos
        this.toggle = this.toggle.bind(this);
        this.open = this.open.bind(this);
        this.close = this.close.bind(this);
        this.handleClickOutside = this.handleClickOutside.bind(this);
        this.handleResize = this.handleResize.bind(this);
        this.handleKeyDown = this.handleKeyDown.bind(this);
        
        // Inicializa quando o DOM estiver pronto
        if (document.readyState === 'loading') {
            document.addEventListener('DOMContentLoaded', () => this.init());
        } else {
            this.init();
        }
    }
    
    /**
     * Inicializa o componente
     */
    init() {
        // Encontra os elementos no DOM
        this.container = document.querySelector(this.settings.containerSelector);
        
        if (!this.container) {
            console.warn(`Elemento container não encontrado com o seletor: ${this.settings.containerSelector}`);
            return;
        }
        
        this.panel = this.container.querySelector(this.settings.panelSelector);
        this.icon = this.container.querySelector(this.settings.iconSelector);
        this.closeButton = this.container.querySelector(this.settings.closeButtonSelector);
        
        // Verifica se os elementos necessários existem
        if (!this.panel || !this.icon) {
            console.warn('Elementos do painel de atalhos não encontrados.');
            return;
        }
        
        // Garante que o container esteja no final do body
        this.moveContainerToBody();
        
        // Adiciona eventos
        this.addEventListeners();
        
        // Inicializa o estado do painel
        this.panel.style.transition = `transform ${this.settings.animationDuration}ms ease-in-out`;
        this.close(); // Fecha o painel por padrão
    }
    
    /**
     * Move o container para o final do body se necessário
     */
    moveContainerToBody() {
        if (this.container.parentNode !== document.body) {
            document.body.appendChild(this.container);
        }
    }
    
    /**
     * Adiciona os listeners de eventos
     */
    addEventListeners() {
        // Toggle no clique do ícone
        if (this.icon) {
            this.icon.addEventListener('click', this.toggle);
        }
        
        // Fecha ao clicar no botão de fechar
        if (this.closeButton) {
            this.closeButton.addEventListener('click', this.close);
        }
        
        // Fecha ao clicar fora do painel
        document.addEventListener('click', this.handleClickOutside);
        
        // Fecha ao pressionar a tecla ESC
        document.addEventListener('keydown', this.handleKeyDown);
        
        // Ajusta o layout em redimensionamentos
        window.addEventListener('resize', this.handleResize);
    }
    
    /**
     * Alterna o estado do painel (aberto/fechado)
     */
    toggle() {
        if (this.isOpen) {
            this.close();
        } else {
            this.open();
        }
    }
    
    /**
     * Abre o painel
     */
    open() {
        this.isOpen = true;
        this.container.classList.add(this.settings.activeClass);
        this.panel.style.transform = 'translateX(0)';
        
        // Dispara evento personalizado
        this.dispatchEvent('shortcutPanel:opened');
    }
    
    /**
     * Fecha o painel
     */
    close() {
        this.isOpen = false;
        this.container.classList.remove(this.settings.activeClass);
        this.panel.style.transform = 'translateX(100%)';
        
        // Dispara evento personalizado
        this.dispatchEvent('shortcutPanel:closed');
    }
    
    /**
     * Manipula o clique fora do painel
     * @param {Event} event - Objeto de evento
     */
    handleClickOutside(event) {
        if (!this.isOpen) return;
        
        const isClickInside = this.container.contains(event.target) || 
                            (this.icon && this.icon.contains(event.target));
        
        if (!isClickInside) {
            this.close();
        }
    }
    
    /**
     * Manipula o redimensionamento da janela
     */
    handleResize() {
        // Fecha o painel em telas pequenas quando redimensionado para uma largura maior
        if (window.innerWidth >= 992 && this.isOpen) {
            this.close();
        }
    }
    
    /**
     * Manipula o pressionamento de teclas
     * @param {KeyboardEvent} event - Objeto de evento de teclado
     */
    handleKeyDown(event) {
        // Fecha ao pressionar ESC
        if (event.key === 'Escape' && this.isOpen) {
            this.close();
        }
    }
    
    /**
     * Dispara um evento personalizado
     * @param {string} eventName - Nome do evento
     * @param {Object} detail - Dados adicionais do evento
     */
    dispatchEvent(eventName, detail = {}) {
        const event = new CustomEvent(eventName, {
            bubbles: true,
            detail: {
                target: this.container,
                ...detail
            }
        });
        
        this.container.dispatchEvent(event);
    }
    
    /**
     * Remove todos os listeners e limpa recursos
     */
    destroy() {
        // Remove event listeners
        if (this.icon) {
            this.icon.removeEventListener('click', this.toggle);
        }
        
        if (this.closeButton) {
            this.closeButton.removeEventListener('click', this.close);
        }
        
        document.removeEventListener('click', this.handleClickOutside);
        document.removeEventListener('keydown', this.handleKeyDown);
        window.removeEventListener('resize', this.handleResize);
    }
}

// Inicializa automaticamente se o módulo for carregado diretamente no navegador
if (typeof window !== 'undefined') {
    window.ShortcutPanel = ShortcutPanel;
    
    // Inicializa automaticamente quando o DOM estiver pronto
    document.addEventListener('DOMContentLoaded', () => {
        new ShortcutPanel();
    });
}

export default ShortcutPanel;
