/**
 * Validação de formulários no lado do cliente
 * Módulo ES6 para validação de formulários
 * 
 * @module FormValidator
 * @version 1.0.0
 */

export class FormValidator {
    /**
     * Inicializa o validador de formulários
     * @param {Object} options - Opções de configuração
     */
    constructor(options = {}) {
        this.settings = {
            formSelector: 'form.uenf-validate',
            errorClass: 'is-invalid',
            validClass: 'is-valid',
            errorElement: 'div',
            ...options
        };
        
        this.forms = [];
        
        // Inicializa quando o DOM estiver pronto
        if (document.readyState === 'loading') {
            document.addEventListener('DOMContentLoaded', () => this.init());
        } else {
            this.init();
        }
    }
    
    /**
     * Inicializa o validador para todos os formulários correspondentes
     */
    init() {
        const forms = document.querySelectorAll(this.settings.formSelector);
        
        if (!forms.length) {
            console.warn('Nenhum formulário encontrado com o seletor:', this.settings.formSelector);
            return;
        }
        
        // Inicializa a validação para cada formulário
        forms.forEach(form => {
            this.initFormValidation(form);
        });
    }
    
    /**
     * Inicializa a validação para um formulário específico
     * @param {HTMLElement} form - Elemento do formulário
     */
    initFormValidation(form) {
        // Verifica se o jQuery Validation está disponível
        if (typeof jQuery === 'undefined' || !jQuery.fn.validate) {
            console.warn('jQuery Validation não está disponível. A validação de formulários não será ativada.');
            return;
        }
        
        const $form = jQuery(form);
        
        // Configuração do validador
        $form.validate({
            errorClass: this.settings.errorClass,
            validClass: this.settings.validClass,
            errorElement: this.settings.errorElement,
            
            errorPlacement: (error, element) => {
                const $field = jQuery(element).closest('.form-group, .form-floating, .form-check');
                if ($field.length) {
                    $field.append(error);
                } else {
                    jQuery(element).after(error);
                }
            },
            
            highlight: (element, errorClass, validClass) => {
                const $element = jQuery(element);
                $element.addClass(errorClass).removeClass(validClass);
                $element.closest('.form-group, .form-floating, .form-check').addClass('has-error');
            },
            
            unhighlight: (element, errorClass, validClass) => {
                const $element = jQuery(element);
                $element.removeClass(errorClass).addClass(validClass);
                $element.closest('.form-group, .form-floating, .form-check').removeClass('has-error');
            },
            
            submitHandler: (form) => {
                this.handleFormSubmit(form);
            }
        });
        
        // Configura validação em tempo real
        this.setupRealTimeValidation($form);
        
        // Armazena referência ao formulário
        this.forms.push($form);
    }
    
    /**
     * Configura a validação em tempo real
     * @param {jQuery} $form - Objeto jQuery do formulário
     */
    setupRealTimeValidation($form) {
        $form.on('keyup change', 'input, select, textarea', function() {
            // Valida apenas o campo alterado
            $form.validate().element(this);
        });
    }
    
    /**
     * Manipula o envio do formulário
     * @param {HTMLElement} form - Elemento do formulário
     */
    handleFormSubmit(form) {
        const $form = jQuery(form);
        const $submitButton = $form.find('button[type="submit"], input[type="submit"]');
        const originalText = $submitButton.html();
        
        // Desabilita o botão de envio
        $submitButton.prop('disabled', true).html('<span class="spinner-border spinner-border-sm" role="status" aria-hidden="true"></span> Enviando...');
        
        // Aqui você pode adicionar lógica de envio AJAX se necessário
        // Por enquanto, apenas envia o formulário normalmente
        form.submit();
        
        // Reativa o botão após 5 segundos (caso haja algum erro)
        setTimeout(() => {
            $submitButton.prop('disabled', false).html(originalText);
        }, 5000);
    }
}

// Inicializa automaticamente se o módulo for carregado diretamente no navegador
if (typeof window !== 'undefined') {
    window.FormValidator = FormValidator;
    
    // Inicializa automaticamente quando o DOM estiver pronto
    document.addEventListener('DOMContentLoaded', () => {
        new FormValidator();
    });
}

export default FormValidator;
