/**
 * Responsive Tables Module
 * 
 * Este módulo torna as tabelas responsivas, envolvendo-as em um container
 * com scroll horizontal em telas menores.
 * 
 * @module ResponsiveTables
 * @version 1.0.0
 */

export class ResponsiveTables {
  /**
   * Cria uma nova instância do ResponsiveTables
   * @param {Object} options - Opções de configuração
   */
  constructor(options = {}) {
    // Configurações padrão
    this.settings = {
      // Seletor para as tabelas que devem ser responsivas
      tableSelector: 'table:not(.no-responsive)',
      
      // Classe CSS para o container responsivo
      containerClass: 'table-responsive',
      
      // Seletor opcional para ignorar tabelas específicas
      excludeSelector: null,
      
      // Largura mínima (em pixels) para ativar o scroll horizontal
      minWidth: 768,
      
      // Sobrescreve as configurações padrão com as opções fornecidas
      ...options
    };
    
    // Referência às tabelas processadas
    this.tables = [];
    
    // Bind de métodos
    this.init = this.init.bind(this);
    this.makeTableResponsive = this.makeTableResponsive.bind(this);
    this.undoTableResponsive = this.undoTableResponsive.bind(this);
    this.handleResize = this.handleResize.bind(this);
    
    // Inicializa quando o DOM estiver pronto
    if (document.readyState === 'loading') {
      document.addEventListener('DOMContentLoaded', () => this.init());
    } else {
      this.init();
    }
  }
  
  /**
   * Inicializa o módulo
   */
  init() {
    // Encontra todas as tabelas que correspondem ao seletor
    const tables = document.querySelectorAll(this.settings.tableSelector);
    
    // Filtra as tabelas que devem ser ignoradas
    const tablesToProcess = this.settings.excludeSelector 
      ? Array.from(tables).filter(table => !table.matches(this.settings.excludeSelector))
      : tables;
    
    // Processa cada tabela
    tablesToProcess.forEach(table => {
      this.makeTableResponsive(table);
    });
    
    // Adiciona listener para redimensionamento da janela
    window.addEventListener('resize', this.handleResize);
    
    console.log(`[ResponsiveTables] ${tablesToProcess.length} tabelas processadas`);
  }
  
  /**
   * Torna uma tabela responsiva
   * @param {HTMLElement} table - Elemento da tabela a ser processado
   */
  makeTableResponsive(table) {
    // Se a tabela já foi processada, ignora
    if (table.dataset.responsiveProcessed === 'true') {
      return;
    }
    
    // Cria o container responsivo
    const container = document.createElement('div');
    container.className = this.settings.containerClass;
    
    // Adiciona um wrapper para o scroll
    const wrapper = document.createElement('div');
    wrapper.className = 'table-wrapper';
    
    // Move a tabela para dentro do wrapper
    const parent = table.parentNode;
    parent.insertBefore(container, table);
    container.appendChild(wrapper);
    wrapper.appendChild(table);
    
    // Adiciona um indicador visual de scroll em telas pequenas
    const scrollIndicator = document.createElement('div');
    scrollIndicator.className = 'table-scroll-indicator';
    scrollIndicator.textContent = 'Role para ver mais →';
    container.appendChild(scrollIndicator);
    
    // Marca a tabela como processada
    table.dataset.responsiveProcessed = 'true';
    
    // Armazena referência para desfazer a alteração se necessário
    this.tables.push({
      table,
      container,
      wrapper,
      parent,
      scrollIndicator
    });
    
    // Ajusta o indicador de scroll com base no tamanho da tela
    this.updateScrollIndicator(container);
    
    return container;
  }
  
  /**
   * Remove o comportamento responsivo de uma tabela
   * @param {HTMLElement} table - Tabela a ter o comportamento removido
   */
  undoTableResponsive(table) {
    const index = this.tables.findIndex(item => item.table === table);
    
    if (index !== -1) {
      const { container, wrapper, parent } = this.tables[index];
      
      // Move a tabela de volta para o local original
      parent.insertBefore(table, container);
      
      // Remove o container e o wrapper
      container.parentNode.removeChild(container);
      
      // Remove a tabela da lista de processadas
      this.tables.splice(index, 1);
      
      // Remove o atributo de processado
      delete table.dataset.responsiveProcessed;
      
      return true;
    }
    
    return false;
  }
  
  /**
   * Atualiza o indicador de scroll com base no tamanho da tela
   * @param {HTMLElement} container - Container da tabela
   */
  updateScrollIndicator(container) {
    const table = container.querySelector('table');
    const wrapper = container.querySelector('.table-wrapper');
    const indicator = container.querySelector('.table-scroll-indicator');
    
    if (!table || !wrapper || !indicator) return;
    
    // Verifica se há overflow horizontal
    const hasOverflow = table.scrollWidth > wrapper.clientWidth;
    
    // Mostra ou esconde o indicador com base no overflow e no tamanho da tela
    if (hasOverflow && window.innerWidth < this.settings.minWidth) {
      indicator.style.display = 'block';
    } else {
      indicator.style.display = 'none';
    }
  }
  
  /**
   * Manipula o redimensionamento da janela
   */
  handleResize() {
    // Debounce para evitar múltiplas execuções
    if (this.resizeTimeout) {
      clearTimeout(this.resizeTimeout);
    }
    
    this.resizeTimeout = setTimeout(() => {
      this.tables.forEach(({ container }) => {
        this.updateScrollIndicator(container);
      });
    }, 100);
  }
  
  /**
   * Destrói a instância e limpa os event listeners
   */
  destroy() {
    // Remove o listener de redimensionamento
    window.removeEventListener('resize', this.handleResize);
    
    // Remove o comportamento responsivo de todas as tabelas
    while (this.tables.length > 0) {
      this.undoTableResponsive(this.tables[0].table);
    }
    
    console.log('[ResponsiveTables] Módulo desativado');
  }
}

// Auto-inicializa se este arquivo for carregado diretamente
if (typeof window !== 'undefined') {
  // Cria uma instância global
  window.ResponsiveTables = ResponsiveTables;
  
  // Auto-inicializa com configurações padrão
  document.addEventListener('DOMContentLoaded', () => {
    window.uenfResponsiveTables = new ResponsiveTables();
  });
}

export default ResponsiveTables;
