/**
 * Tema UENF - Main JavaScript
 * 
 * Este é o ponto de entrada principal para o JavaScript do tema.
 * Aqui, importamos e inicializamos todos os módulos necessários.
 */

// Importar módulos
import { MobileMenu } from './components/mobile-menu';
import { SmoothScroll } from './utils/smooth-scroll';
import { ShortcutPanel } from './components/shortcut-panel';
import { FormValidator } from './modules/form-validator';
import { StickyHeader } from './components/sticky-header';
import { ResponsiveTables } from './utils/responsive-tables';

// Configurações globais
const CONFIG = {
  // Configurações das tabelas responsivas
  responsiveTables: {
    tableSelector: 'table:not(.no-responsive)',
    containerClass: 'table-responsive',
    minWidth: 768
  },
  
  // Configurações do menu mobile
  mobileMenu: {
    menuToggle: '.menu-toggle',
    mainNav: '.main-navigation',
    activeClass: 'toggled'
  },
  
  // Configurações do cabeçalho fixo
  stickyHeader: {
    headerSelector: '.site-header',
    stickyClass: 'header-sticky',
    hiddenClass: 'header-hidden',
    stickyOffset: 100,
    scrollThreshold: 5,
    enableOnMobile: false
  },
  
  // Configurações de rolagem suave
  smoothScroll: {
    offset: 80, // Ajuste baseado na altura do cabeçalho
    duration: 800,
    easing: 'easeInOutCubic'
  },
  
  // Configurações do painel de atalhos
  shortcutPanel: {
    containerSelector: '#uenf-shortcut-panel-container',
    panelSelector: '.shortcut-panel',
    iconSelector: '.shortcut-icon',
    closeButtonSelector: '.close-panel',
    activeClass: 'active',
    animationDuration: 300,
    enableSmoothScroll: true
  },
  
  // Configurações do validador de formulários
  formValidator: {
    formSelector: 'form.uenf-validate',
    errorClass: 'is-invalid',
    validClass: 'is-valid'
  }
};

// Classe principal do tema
class UENFTheme {
  constructor() {
    // Inicializar módulos
    this.modules = {
      mobileMenu: null,
      smoothScroll: null,
      shortcutPanel: null,
      formValidator: null,
      stickyHeader: null,
      responsiveTables: null
    };
    
    // Inicializar quando o DOM estiver pronto
    if (document.readyState === 'loading') {
      document.addEventListener('DOMContentLoaded', () => this.init());
    } else {
      this.init();
    }
  }
  
  /**
   * Inicializar todos os módulos
   */
  init() {
    // Inicializar menu mobile
    this.initMobileMenu();
    
    // Inicializar rolagem suave
    this.initSmoothScroll();
    
    // Inicializar painel de atalhos
    this.initShortcutPanel();
    
    // Inicializar validador de formulários
    this.initFormValidator();
    
    // Inicializar outros módulos aqui
    this.initStickyHeader();
    this.initBackToTop();
    this.initResponsiveTables();
    
    // Disparar evento personalizado quando o tema for inicializado
    document.dispatchEvent(new CustomEvent('uenf:theme:init'));
  }
  
  /**
   * Inicializar menu mobile
   */
  initMobileMenu() {
    try {
      this.modules.mobileMenu = new MobileMenu(CONFIG.mobileMenu);
    } catch (error) {
      console.error('Erro ao inicializar menu mobile:', error);
    }
  }
  
  /**
   * Inicializar rolagem suave
   */
  initSmoothScroll() {
    try {
      this.modules.smoothScroll = new SmoothScroll(CONFIG.smoothScroll);
    } catch (error) {
      console.error('Erro ao inicializar rolagem suave:', error);
    }
  }
  
  /**
   * Inicializar painel de atalhos
   */
  initShortcutPanel() {
    try {
      this.modules.shortcutPanel = new ShortcutPanel(CONFIG.shortcutPanel);
      
      // Eventos personalizados do painel de atalhos
      const container = document.querySelector(CONFIG.shortcutPanel.containerSelector);
      if (container) {
        container.addEventListener('shortcutPanel:opened', (e) => {
          console.log('Painel de atalhos aberto', e.detail);
        });
        
        container.addEventListener('shortcutPanel:closed', (e) => {
          console.log('Painel de atalhos fechado', e.detail);
        });
      }
    } catch (error) {
      console.error('Erro ao inicializar painel de atalhos:', error);
    }
  }
  
  /**
   * Inicializar validador de formulários
   */
  initFormValidator() {
    try {
      this.modules.formValidator = new FormValidator(CONFIG.formValidator);
    } catch (error) {
      console.error('Erro ao inicializar validador de formulários:', error);
    }
  }
  
  /**
   * Inicializar cabeçalho fixo
   */
  initStickyHeader() {
    try {
      this.modules.stickyHeader = new StickyHeader(CONFIG.stickyHeader);
    } catch (error) {
      console.error('Erro ao inicializar cabeçalho fixo:', error);
    }
  }
  
  /**
   * Botão "Voltar ao topo"
   */
  initBackToTop() {
    const backToTopBtn = document.querySelector('.back-to-top');
    
    if (backToTopBtn) {
      // Mostrar/ocultar botão ao rolar
      window.addEventListener('scroll', () => {
        if (window.pageYOffset > 300) {
          backToTopBtn.classList.add('show');
        } else {
          backToTopBtn.classList.remove('show');
        }
      });
      
      // Rolar suavemente para o topo ao clicar
      backToTopBtn.addEventListener('click', (e) => {
        e.preventDefault();
        window.scrollTo({
          top: 0,
          behavior: 'smooth'
        });
      });
    }
  }
  
  /**
   * Validação de formulários
   */
  initFormValidation() {
    // Selecionar todos os formulários que precisam de validação
    const forms = document.querySelectorAll('form[data-validate]');
    
    forms.forEach(form => {
      form.addEventListener('submit', (e) => {
        let isValid = true;
        const requiredFields = form.querySelectorAll('[required]');
        
        // Validar campos obrigatórios
        requiredFields.forEach(field => {
          if (!field.value.trim()) {
            isValid = false;
            field.classList.add('is-invalid');
            
            // Adicionar mensagem de erro se não existir
            if (!field.nextElementSibling || !field.nextElementSibling.classList.contains('invalid-feedback')) {
              const errorMsg = document.createElement('div');
              errorMsg.className = 'invalid-feedback';
              errorMsg.textContent = field.dataset.error || 'Este campo é obrigatório.';
              field.parentNode.insertBefore(errorMsg, field.nextSibling);
            }
          } else {
            field.classList.remove('is-invalid');
            
            // Remover mensagem de erro se existir
            if (field.nextElementSibling && field.nextElementSibling.classList.contains('invalid-feedback')) {
              field.nextElementSibling.remove();
            }
            
            // Validação de e-mail
            if (field.type === 'email' && !this.isValidEmail(field.value)) {
              isValid = false;
              field.classList.add('is-invalid');
              
              const errorMsg = document.createElement('div');
              errorMsg.className = 'invalid-feedback';
              errorMsg.textContent = 'Por favor, insira um endereço de e-mail válido.';
              field.parentNode.insertBefore(errorMsg, field.nextSibling);
            }
          }
        });
        
        // Impedir envio se o formulário não for válido
        if (!isValid) {
          e.preventDefault();
          
          // Rolar até o primeiro campo inválido
          const firstInvalid = form.querySelector('.is-invalid');
          if (firstInvalid) {
            firstInvalid.scrollIntoView({
              behavior: 'smooth',
              block: 'center'
            });
          }
        }
      });
      
      // Remover classe de erro ao digitar
      form.addEventListener('input', (e) => {
        if (e.target.hasAttribute('required')) {
          e.target.classList.remove('is-invalid');
          
          if (e.target.nextElementSibling && e.target.nextElementSibling.classList.contains('invalid-feedback')) {
            e.target.nextElementSibling.remove();
          }
        }
      });
    });
  }
  
  /**
   * Tabelas responsivas
   */
  initResponsiveTables() {
    try {
      this.modules.responsiveTables = new ResponsiveTables(CONFIG.responsiveTables);
    } catch (error) {
      console.error('Erro ao inicializar tabelas responsivas:', error);
      
      // Fallback para implementação simples em caso de erro
      const tables = document.querySelectorAll('table:not(.no-responsive)');
      
      tables.forEach(table => {
        // Criar container para a tabela
        const container = document.createElement('div');
        container.className = 'table-responsive';
        
        // Envolver a tabela no container
        table.parentNode.insertBefore(container, table);
        container.appendChild(table);
      });
    }
  }
  
  /**
   * Validar endereço de e-mail
   * @param {string} email - O e-mail a ser validado
   * @returns {boolean} - Retorna true se o e-mail for válido
   */
  isValidEmail(email) {
    const re = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
    return re.test(String(email).toLowerCase());
  }
  
  /**
   * Destruir instância e limpar eventos
   */
  destroy() {
    // Destruir todos os módulos
    Object.values(this.modules).forEach(module => {
      if (module && typeof module.destroy === 'function') {
        try {
          module.destroy();
        } catch (error) {
          console.error('Erro ao destruir módulo:', error);
        }
      }
    });
    
    // Remover event listeners adicionais aqui
  }
}

// Inicializar o tema
document.addEventListener('DOMContentLoaded', () => {
  // Criar instância global do tema
  window.uenfTheme = new UENFTheme();
  
  // Expor módulos específicos globalmente, se necessário
  window.UENF = {
    modules: {
      MobileMenu,
      SmoothScroll,
      StickyHeader,
      ResponsiveTables
    },
    
    // Método para acessar a instância do tema
    getInstance: () => window.uenfTheme
  };
});

// Suporte para módulos CommonJS (para compatibilidade com Webpack/Babel)
if (typeof module !== 'undefined' && module.exports) {
  module.exports = {
    MobileMenu,
    SmoothScroll,
    UENFTheme
  };
}
